.search-shops {
    &__select {
        width: 33.6rem;

        @media (max-width: 500px) {
            width: 100%;
        }

        &:not(:last-child) {
            margin-bottom: 3.2rem;
        }
    }

    &__grid {
        display: grid;
        grid-gap: 5.2rem;
        padding-top: 5.2rem;

        @media (max-width: 500px) {
            padding-top: 3rem;
        }

        @media (max-width: 1024px) {
            grid-gap: 3rem;
        }

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: 1025px) {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    &__item {
        &.hidden {
            display: none;
        }
    }
}

#shops-map {
    height: 50rem;

    @media (max-width: 500px) {
        height: 40rem;
    }

    > * {
        width: 100% !important;
        height: 100% !important;

        > * {
            width: 100% !important;
            height: 100% !important;
        }
    }
}
